
import Weather from '@/services/enum/Weather';
import getPrioritizedEnumValues from 'brdgm-commons/src/util/enum/getPrioritizedEnumValues';
import { defineComponent, PropType } from 'vue'
import AppIcon from './AppIcon.vue'

export default defineComponent({
  name: 'WeatherPriority',
  components: {
    AppIcon
  },
  props: {
    weather: {
      type: String as PropType<Weather>,
      required: false
    },
    weathers: {
      type: Object as PropType<Weather[]>,
      required: false
    }
  },
  computed: {
    allWeathers() : Weather[] {
      if (this.weathers) {
        return this.weathers
      }
      else {
        return getPrioritizedEnumValues(Weather, this.weather ?? Weather.RAIN)
      }
    }
  }
})
